body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Arial, Helvetica, sans-serif;
  height: 100%;
  margin: 0;
}
p {
  line-height: 1.7;
  font-size: 1.1rem;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Raleway', sans-serif;
  text-transform: uppercase;
  font-weight: 100;
}
h1 {
  font-size: 6rem;
}
h2 {
  font-weight: 400;
  font-size: 3.2rem;
}
h3 {
  font-weight: 300;
  font-size: 1.8rem;
}
h4 {
  font-weight: 200;
}
h5 {
  font-weight: 600;
  font-size: 1.2rem;
}
h6 {
  font-weight: 600;
  font-size: 1rem;
}
ul, li {
  display: block;
}
.navbar {
  padding: 0 5rem;
}
.light-text {
  color: #666;
  font-size: 1rem;
}
.sm-text {
  color: #666;
  font-size: .85rem;
}
.accent-text {
  color: #7ca5c5;
}
.input-feedback {
  color: #dc3545;
  font-size: .8rem;
}
.no-margin > p{
  margin: 0;
}
.badge {
  color: #fff;
  background-color: #7ca5c5;
  display: inline-block;
  padding: .35em .5em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: .25rem;
  -webkit-transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
.btn {
  text-transform: uppercase;
  font-size: .8rem;
}
.btn-primary {
  color: #fff;
  background-color: #7ca5c5;
  border-color: #7ca5c5;
}
.btn-primary:hover {
  color: #fff;
  background-color: #5692c1;
  border-color: #5692c1;
}
.btn-success {
  background-color: #fff;
  border-color: #7ca5c5;
  color: #7ca5c5;

}
.btn-success:hover {
  color: #fff;
  background-color: #7ca5c5;
  border-color: #7ca5c5;
}
.btn-primary:not(:disabled):not(.disabled):active, 
.btn-success:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #5692c1;
  border-color: #5692c1;
}
.btn-primary:not(:disabled):not(.disabled).active:focus, 
.btn-primary:not(:disabled):not(.disabled):active:focus, 
.show>.btn-primary.dropdown-toggle:focus {
  box-shadow:  0 0 0 0.2rem rgba(124,165,197, .5);
}
.btn-success:not(:disabled):not(.disabled).active:focus, 
.btn-success:not(:disabled):not(.disabled):active:focus, 
.show>.btn-success.dropdown-toggle:focus {
    box-shadow: 0 0 0 0.2rem  rgba(124,165,197, .5);
}
.btn-primary:not(:disabled):not(.disabled).active, 
.btn-primary:not(:disabled):not(.disabled):active, 
.show>.btn-primary.dropdown-toggle {
  color: #fff;
  background-color: #5692c1;
  border-color: #5692c1;
}
.btn-primary:focus,
.btn-success:focus {
  box-shadow: 0 0 0 0.2rem rgba(124,165,197, .5);
  border-color: rgba(124,165,197, .5);
}
.btn-primary.disabled, 
.btn-primary:disabled {
    color: #fff;
    background-color: rgba(124,165,197, .5);
    border-color: rgba(124,165,197, .5);
}
.form-group {
  margin-bottom: 2rem;
}
input[type="checkbox"] {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.banner {
  background-color: #fff;
  padding: 80px;
  margin: 80px 0;
  text-align: center;
  border: 2px solid #7ca5c5;
}

/* SPACE SECTION */
.space-section {
  min-height: 37vh;
}
/* ABOUT SECTION */
.about-section {
  margin-top: 6rem;
  justify-content: space-between;
}
.line-one {
  width:25%;
  background: linear-gradient(to top right, #fff calc(50% - 1px), #aaa, #fff calc(50% + 1px) )
}
.line-two {
  width:25%;
  background: linear-gradient(to top right, #fff calc(50% - 1px), #aaa, #fff calc(50% + 1px) )
}
.line-three {
  width:25%;
  background: linear-gradient(to top right, #fff calc(50% - 1px), #aaa, #fff calc(50% + 1px) )
}
@media (max-width: 768px) { 
  .about-section__img--first,
  .about-section__img--second {
    margin: 1rem 5rem 3rem;
  }
}

/* REGISTER SECTION */
.register-section h6 {
  margin-top: 2rem;
}


img.profile {
  max-height: 150px;
}
/* CARD */
.card {
  border: none;
}
.card-body {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.card-body .container,
.card-body .col {
  padding: 0;
}
.card-header {
  background-color: #fff;
}
.card-header:hover {
  background-color: #f8f9fa;
}
/* TYPOGRAPHY */
.preview h3 {
  margin: 3rem 0;
}
.preview h3,
.preview h5 {
  text-align: center;
  padding-bottom: 1rem;
}
/* CLASS */
.class-preview .details {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.class-description {
  margin: 1rem;
}
/* PERFORMANCE */
.performance-preview p {
  text-align: center;
}
.performance-preview .btn {
  margin: 1.5rem;
}
/* AUDITION */
.audition-preview .card-body {
  background-color: rgba(0, 0, 0, .04);
  border-bottom: 1px solid rgba(0,0,0,.125);
}
.audition-header .date {
  border: 1px solid #eee;
  width: 4rem;
  height: 4rem;
}
.audition-header .day,
.audition-header .month {
  margin: 0;
  text-align: center;
  line-height: 1.4;
} 
.audition-header .day {
  font-size: 2rem;
  color: #222;
}
.audition-header .month { 
  margin-top: -10px;
}
.class-preview .details, 
.audition-header .details { 
  margin:  .5rem 0;
} 
.audition-header h6,
.class-preview .details h6 { 
  margin-bottom: 0;   
}
.audition-header p { 
  color: #888;
  font-size: .8rem;
  margin: 0;
}
.audition-header span { 
  color: #888;
} 
/* SPACE */


@media (max-width: 992px) {
  .preview h3 {
    border-bottom: 1px solid #ddd;
    border-top: 1px solid #ddd;
    padding: 1rem;
  }

}

@media (max-width: 576px) { 
  .banner {
    padding: 8vw;
  }
}


div.thumbnail {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  padding-top: 100%;
}

header {
  margin-bottom: 100px;
  padding-bottom: 55px;
}
.hero {
  position: relative;
  background-color: black;
  height: 50vh; 
  min-height: 25rem;
  width: 100%;
  overflow: hidden;
}

.hero video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: 0;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

.hero .container {
  position: relative;
  z-index: 2;
}

.hero .overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: black;
  opacity: 0.25;
  z-index: 1;
}

@media (pointer: coarse) and (hover: none) {
  .hero {
    background: url('https://source.unsplash.com/XT5OInaElMw/1600x900') black no-repeat center center scroll;
  }
  .hero video {
    display: none;
  }
}


.list {
  border: none;
  margin-bottom: 2rem;
}
.class-list_title,
.space-list_title {
  display: flex;
  justify-content: flex-start;
  align-items: self-start;
}
.space-list_details {
  display: flex;
}
.space-list_details > p {
  margin-right: 1rem; 
}
.list .badge {
  margin: .3rem 1rem;
}


.section-wrapper {
  width: 100%;
  padding: 4vw 8vw;
  background-color: #eee;
  /* background-color: #d0d9e0; */
  /* background-color: #696464; */
  margin-bottom: 10rem;
  border-bottom: 1px solid #ddd;
  border-top: 1px solid #ddd;
}
.calendar-section_content {
  /* background-color: #d0d9e0; */
  /* background-color: #696464; */
  /* background-color: #eee; */
  padding: 3rem;
  max-width: 960px;
  margin: 0 auto;
}
.calendar-section_content .btn {
  margin: 0 auto;
}
.active-date {
  position: absolute;
  bottom: .5rem;
  background-color: #d0d9e0;
  border-radius: 25px;
  width: 67%;
  height: .3rem;
}

.react-calendar,
.calendar-placeholder {
  width: 100%;
  max-width: 480px;
  border: none;
}
.calendar-section_content .form-check {
  padding-left: 2.25rem;
}
.react-calendar__navigation {
  height: 55px;
  border-bottom: 1px solid #eee;
}
.react-calendar__navigation__label,
.react-calendar__year-view,
.react-calendar__decade-view {
  font-family: 'Raleway', sans-serif;
  text-transform: uppercase;
  font-weight: 400;
}
.react-calendar__month-view__weekdays {
  text-decoration: none;
}
.react-calendar__month-view__weekdays abbr {
  font-weight: 500;
  font-family: 'Raleway', sans-serif;
  text-decoration: none;
}
.react-calendar__month-view__days button {
  border: .5px solid #eee;
}
.react-calendar__tile {
  position: relative;
  height: 4rem;
  width: 4rem;
  font-size: 1.5rem;
  color: #222;
}
/* EVENT STYLING */
.react-calendar__tile--active {
  background-color: #7ca5c5;
}
.react-calendar__tile--hasActive {
  background-color: #7ca5c5;
}
.react-calendar__tile--active:enabled:hover, 
.react-calendar__tile--active:enabled:focus {
  background-color: #9ba6ac;
}
.react-calendar__tile--hasActive:enabled:hover, 
.react-calendar__tile--hasActive:enabled:focus {
    background: #7ca5c5;
}

@media (max-width: 768px) { 
  .calendar-wrapper {
    padding: 8vh 3vw;
  }
}
@media (max-width: 576px) { 
  .calendar-section_content {
    padding: 2rem;
  }
  .calendar-wrapper .container {
    margin: 0;
    padding: 0;
  }
  .react-calendar__tile {
    height: 2.7em;
    width: 3em;
    font-size: 4vw;
    color: #222;
  }
}






